<template>
  <div class="message">
    <div class="main">
      <div class="d-module-title">最新留言</div>
      <div class="content">
        <ul class="custom-message">
          <li v-for="item of message" :key="item.id" class="display-flex" :title="item.msg">
            <span class="username">{{item.username}}: </span>
            <span class="msg">{{item.msg}}</span>
            <span class="time">{{item.time}}</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Message',
    data() {
      return {
        url: '/message',
        message: []
      }
    },
    created() {
      this.getMessage();
    },
    methods: {
      getMessage: function() {
        this.$axios.get(this.url + '/queryFive')
          .then(res => {
            if (res.data.code == 200) {
              this.message = res.data.body;
            } else {
              this.$message({
                message: res.data.message,
                type: 'warning'
              })
            }
          })
      }
    }
  }
</script>

<style scoped>
  .message {
    background-color: #f9f9f9;
    box-sizing: border-box;
    padding: 20px;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  }

  .custom-message span {
    display: block;
  }

  .custom-message li {
    margin: 10px 0;
    position: relative;
  }

  .custom-message>li:last-child {
    margin-bottom: 0;
  }

  .username {
    font-size: 12px;
    color: #909399;
  }

  .msg {
    width: 40%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-left: 5px;
  }

  .time {
    position: absolute;
    font-size: 10px;
    color: #909399;
    top: 0;
    right: 0;
  }
</style>



