<template>
  <div class="digest" v-loading="loading">
    <div class="main">
      <div class="content">
        <div class="inner" v-for="(item,index) of article" :key="index">
          <p class="title" @click="goToDetails(item.id)">{{item.article_title}}</p>
          <div class="d-twoBlock"></div>
          <div class="info display-flex">
            <span class="type">{{item.article_type}}</span>
            <span class="time"><i class="el-icon-date"></i> {{item.update_date}}</span>
            <span class="author"><i class="el-icon-edit-outline"></i> {{item.author}}</span>
            <span class="classify"><i class="el-icon-folder-opened"></i> {{item.article_categories}}</span>
          </div>
          <div class="d-oneBlock"></div>
          <div class="desc">
            {{item.article_tabloid}}
          </div>
          <div class="d-twoBlock"></div>
          <div class="tag display-flex">
            <span v-for="(item,index) of item.article_tags.split(',')" :key="index">
              <i class="el-icon-paperclip"></i>
              {{item}}
            </span>
          </div>
        </div>
        <div class="paging">
          <el-pagination background layout="prev, pager, next" :total="article_count" :current-page="$store.state.firstIndex"
            :page-size="5" @current-change="currentChange">
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Digest',
    data() {
      return {
        url: '/article',
        loading: false,
        article: [],
        article_count: 0, // 文章总数
        currentPage: 1
      }
    },
    created() {
      this.getArticleCount();

      if (this.$store.state.firstIndex == 0) {
        this.getLimitArticle(0);
      } else {
        this.getLimitArticle((this.$store.state.firstIndex - 1) * 5);
      }
    },
    methods: {
      // 点击进入详情页
      goToDetails: function(id) {
        this.$router.push({
          path: '/details',
          query: {
            article_id: id
          }
        })
      },
      // 获取数据
      getLimitArticle: function(index) {
        this.article = [];
        this.loading = true;
        this.$axios.get(this.url + '/limit' + `/${index}`).then(res => {
          if (res.data.code == 200) {
            this.article = res.data.body;
            this.loading = false;
          } else {
            this.$message({
              message: res.data.message,
              type: 'warning'
            })
          }
        })
      },
      // 分页获取数据
      currentChange: function(val) {
        this.$store.state.firstIndex = val;
        // this.currentPage = this.$store.state.firstIndex;
        this.getLimitArticle((val - 1) * 5);
      },
      // 获取文章数
      getArticleCount: function() {
        this.$axios.get(this.url + '/articleCount').then(res => {
          if (res.data.code == 200) {
            this.article_count = res.data.body;
          } else {
            this.$message({
              message: res.data.message,
              type: 'warning'
            })
          }
        })
      }
    }
  }
</script>

<style scoped>
  .inner {
    width: 100%;
    box-sizing: border-box;
    padding: 20px;
    background-color: #f9f9f9;
    margin-bottom: 20px;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
    transition: all 0.3s linear;
  }

  .inner:hover {
    background-color: #fff;
  }

  .title {
    font-size: 20px;
    font-weight: bold;
  }

  .title:hover {
    color: #409EFF;
    cursor: pointer;
  }

  .info span {
    display: block;
    margin-right: 10px;
    padding: 3px 5px;
    color: #909399;
  }

  .info span:nth-child(1) {
    background-color: #409EFF;
    color: #fff;
    border-radius: 5px;
  }

  .desc {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 1px;
    color: #606266;
    /* font-family: 微软雅黑, "Microsoft YaHei"; */
  }

  .tag {
    border-top: 2px solid #fff;
    padding-top: 20px;
  }

  .tag span {
    display: block;
    margin-right: 10px;
    font-size: 12px;
  }
</style>



