<template>
  <div class="website">
    <div class="main">
      <div class="d-module-title">网站信息</div>
      <div class="content">
        <div class="inner">
          <i class="el-icon-s-order"></i>
          文章总数：{{article_count}}
        </div>
        <div class="inner">
          <i class="el-icon-paperclip"></i>
          标签总数：{{tag_count}}
        </div>
        <div class="inner">
          <i class="el-icon-s-flag"></i>
          留言总数：{{message_count}}
        </div>
        <div class="inner">
          <i class="el-icon-s-comment"></i>
          评论总数：{{comment_count}}
        </div>
        <div class="inner">
          <i class="el-icon-s-tools"></i>
          安全运行：{{exist_count}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Website',
    data() {
      return {
        url: '/article',
        message_url: '/message',
        article_count: 0,
        tag_count: 0,
        message_count: 0,
        comment_count: 0,
        exist_count: '',
        timer: null
      }
    },
    created() {
      this.getArticleCount();
      this.getMessageCount();
      this.timer = setInterval(() => {
        this.showExsit();
      }, 1000);

      this.getTagsCount();

      this.getCommentCount();
    },
    destroyed() {
      clearInterval(this.timer);
    },
    methods: {
      // 获取文章数
      getArticleCount: function() {
        this.$axios.get(this.url + '/articleCount').then(res => {
          if (res.data.code == 200) {
            this.article_count = res.data.body;
          } else {
            this.$message({
              message: res.data.message,
              type: 'warning'
            })
          }
        })
      },
      // 获取留言数
      getMessageCount: function() {
        this.$axios.get(this.message_url + '/queryCount')
          .then(res => {
            if (res.data.code == 200) {
              this.message_count = res.data.body;
            } else {
              this.$message({
                message: res.data.message,
                type: 'warning'
              })
            }
          })
      },
      // 设置网站运行天数
      NewDate: function(str) {
        str = str.split('-');
        var date = new Date();
        date.setUTCFullYear(str[0], str[1] - 1, str[2]);
        date.setUTCHours(0, 0, 0, 0);
        return date;
      },
      showExsit: function() {
        var birthDay = this.NewDate("2020-11-02"); //此处设置开始时间
        var today = new Date();
        var timeold = today.getTime() - birthDay.getTime();
        var sectimeold = timeold / 1000
        var secondsold = Math.floor(sectimeold);
        var msPerDay = 24 * 60 * 60 * 1000;
        var e_daysold = timeold / msPerDay;
        var daysold = Math.floor(e_daysold);
        var e_hrsold = (daysold - e_daysold) * -24;
        var hrsold = Math.floor(e_hrsold);
        var e_minsold = (hrsold - e_hrsold) * -60;
        var minsold = Math.floor((hrsold - e_hrsold) * -60);
        var seconds = Math.floor((minsold - e_minsold) * -60).toString();
        this.exist_count = daysold + "天" + hrsold + "小时" + minsold + "分" + seconds + "秒";
      },
      // 查询标签总数
      getTagsCount: function() {
        this.$axios.get(this.url + '/queryTags').then(res => {
          if (res.data.code == 200) {
            let tags = [];
            for (let i = 0; i < res.data.body.length; i++) {
              tags = tags.concat(res.data.body[i].article_tags.split(','));
            }
            tags = [...new Set(tags)];
            this.tag_count = tags.length;
          } else {
            this.$message({
              message: res.data.message,
              type: 'warning'
            })
          }
        })
      },
      // 获取评论总数
      getCommentCount: function() {
        this.$axios.get('/comment/getCommentCount').then(res => {
          if (res.data.code == 200) {
            this.comment_count = res.data.body;
          } else {
            this.$message({
              message: res.data.message,
              type: 'warning'
            })
          }
        })
      }
    }
  }
</script>

<style scoped>
  div {
    font-size: 14px;
  }

  .website {
    padding: 20px;
    box-sizing: border-box;
    background-color: #f9f9f9;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  }

  .inner {
    margin: 5px 0;
  }

  .content .inner:first-child {
    margin-top: 10px;
  }

  .content .inner:last-child {
    margin-bottom: 0;
  }
</style>



