<template>
  <div class="inform">
    <div class="main">
      <div class="d-module-title">公告</div>
      <div class="content">
        <div class="inner">
          {{inform.content}}
          <span class="time">{{inform.time}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Inform',
    data() {
      return {
        inform: {
          id: 1,
          content: '个人博客开发告一段落，欢迎大家来发布文章',
          time: '2020-11-20'
        }
      }
    }
  }
</script>

<style scoped>
  div{
    font-size: 14px;
  }

  .inform {
    box-sizing: border-box;
    padding: 20px;
    background-color: #f9f9f9;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  }

  .inner {
    position: relative;
    box-sizing: border-box;
    padding-right: 40%;
    line-height: 18px;
  }

  .time {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    font-size: 12px;
    color: #fff;
    background-color: #E6A23C;
    padding: 3px;
    border-radius: 3px;
  }
</style>



