<template>
  <div class="home">
    <div class="main">
      <div class="content display-flex">
        <div class="aside">
          <Introduce></Introduce>
          <div class="d-fourBlock"></div>
          <Message></Message>
          <div class="d-fourBlock"></div>
          <Inform></Inform>
          <div class="d-fourBlock"></div>
          <Website></Website>
        </div>
        <div class="container">
          <Digest></Digest>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Website from '@/components/home/Website'
  import Inform from '@/components/home/Inform'
  import Digest from '@/components/home/Digest'
  import Message from '@/components/home/Message'
  import Introduce from '@/components/home/Introduce'
  export default {
    name: 'Home',
    components: {
      Introduce,
      Message,
      Digest,
      Inform,
      Website
    }
  }
</script>

<style scoped>
  .content {
    width: 100%;
    justify-content: space-between;
    align-items: flex-start;
  }

  .aside {
    width: 25%;
  }

  .container {
    width: 70%;
    box-sizing: border-box;
  }
</style>



